@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
button{
    background: none;
    border: none;
    outline: none;
    cursor: pointer;
}
input{
    outline: none;
}
body{
    background-color: #E4F9F5;
    min-height: 100vh;
    position: relative;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}
.container{
    max-width: 1200px;
    padding: 0 30px;
    margin: 0 auto;
}
.flex{
    display: flex;
}
@media(max-width: 1200px){
    .container{
        max-width: 980px;
    }
}