@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mansalva&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
button{
    background: none;
    border: none;
    outline: none;
    cursor: pointer;
}
input{
    outline: none;
}
body{
    background-color: #E4F9F5;
    min-height: 100vh;
    position: relative;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}
.container{
    max-width: 1200px;
    padding: 0 30px;
    margin: 0 auto;
}
.flex{
    display: flex;
}
@media(max-width: 1200px){
    .container{
        max-width: 980px;
    }
}
.main {
  position: relative;
}

.main::after {
  content: url(/meditate/static/media/elipse-1.b4215077.svg);
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: -1;
}

.main__body {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.main__title {
  font-size: 48px;
  font-family: Roboto;
  margin-bottom: 90px;
  color: #40514e;
}

.main__row {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 51px;
}

.main__column {
  text-align: center;
  background-color: #40514E;
  flex: 0 0 32.19%;
  border-radius: 15px;
  padding: 22px 50px 54px;
  position: relative;
}

.main__column:last-child {
  background-color: #11999E;
}

.main__column:first-child::after {
  content: url(/meditate/static/media/array-1.a8698512.svg);
  position: absolute;
  bottom: -156px;
  left: 152px;
}

.main__column:nth-child(2)::after {
  content: url(/meditate/static/media/elipse-2.37773965.svg);
  position: absolute;
  top: -132px;
  right: -248px;
  z-index: -1;
}

.main__step {
  font-size: 48px;
  color: #fff;
  margin-bottom: 36px;
}

.main__text {
  font-size: 24px;
  color: #fff;
  max-width: 260px;
  margin: 0 auto;
}

.main__btn {
  color: #fff;
  text-decoration: none;
  font-size: 24px;
  background-color: #11999E;
  border-radius: 15px;
  padding: 17px 29px;
  position: relative;
}

.main__btn::after {
  content: url(/meditate/static/media/array-2.6cbf0a84.svg);
  position: absolute;
  bottom: -270px;
  right: -260px;
}

@media (max-width: 1200px) {
  .main__title {
    font-size: 36px;
    margin-bottom: 60px;
  }
  .main__step {
    font-size: 36px;
  }
  .main__text {
    font-size: 20px;
  }
  .main__row {
    margin-bottom: 35px;
  }
  .main__column {
    padding: 20px 40px 40px;
  }
  .main__column:first-child::after {
    transform: scale(0.7);
    bottom: -135px;
    left: 110px;
  }
  .main__column:nth-child(2)::after {
    transform: scale(0.7);
    right: -200px;
  }
  .main__btn {
    font-size: 20px;
  }
  .main__btn::after {
    transform: scale(0.7);
    bottom: -240px;
    right: -220px;
  }
  .main::after {
    transform: scale(0.7);
    bottom: -35px;
    left: -45px;
  }
}
/*# sourceMappingURL=main.css.map */
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #11999E;
  z-index: 20;
}

.header__body {
  display: flex;
  justify-content: space-between;
  font-family: Roboto;
  align-items: center;
  min-height: 90px;
}

.header__list {
  list-style: none;
  display: flex;
}

.header__list li {
  margin-right: 70px;
}

.header__list li:last-child {
  margin-right: 0;
}

.header__item {
  font-size: 28px;
  text-decoration: none;
  color: #fff;
}

@media (max-width: 1200px) {
  .header__body {
    min-height: 70px;
  }
  .header__logo, .header__logo rect {
    height: 70px;
    width: 70px;
  }
  .header__item {
    font-size: 20px;
  }
}
/*# sourceMappingURL=header.css.map */
.music__body {
  padding-top: 133px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.music__search {
  display: flex;
  margin-bottom: 64px;
}

.music__btn {
  padding: 15px 63px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.music__btn::after {
  display: none;
}

.music__input {
  border-radius: 15px;
  border: 4px solid #11999E;
  width: 463px;
  padding: 14px 16px 10px;
  font-size: 24px;
}

.music__name {
  font-size: 24px;
  text-align: center;
  max-width: 120px;
  margin: 0 auto;
}

.music__list {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
}

.music__column {
  background: whitesmoke;
  border-radius: 15px;
  padding: 14px 18px 20px;
  box-shadow: -2px 2px 9px rgba(0, 0, 0, 0.25);
  height: 240px;
  width: 173px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-right: 20px;
  margin-bottom: 40px;
  cursor: pointer;
}

.music__column:nth-child(6n) {
  margin-right: 0;
}

.music__img {
  height: 155px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.music__current {
  border: 5px solid #11999e;
}
/*# sourceMappingURL=music.css.map */
.footer {
  position: fixed;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: #fff;
}

.footer__progress {
  background-color: #E6E6E6;
  width: 100%;
  height: 12px;
  display: flex;
  justify-content: space-between;
}

.footer__progress span {
  flex: 0 0 50%;
}

.footer__progress span.progress {
  background-color: rgba(17, 153, 158, 0.5);
}

.footer__progress span.completed {
  background-color: #11999e;
}

.footer__body {
  display: flex;
  width: 100%;
  padding: 24px 0 36px;
}

.footer__column {
  flex: 0 0 25%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #40514E;
  font-size: 24px;
}

.footer__column span {
  color: #929292;
  font-size: 24px;
}

.footer__btn {
  padding: 14px 40px;
}

.footer__btn::after {
  display: none;
}

.footer__btn.disabled {
  opacity: .6;
}
/*# sourceMappingURL=footer.css.map */
.time__body {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: Mansalva;
  font-size: 144px;
  color: #40514E;
}

.time__column {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.time__column:last-child {
  color: #11999E;
}
/*# sourceMappingURL=time.css.map */
.timer__body {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.timer__circle {
  width: 611px;
  height: 611px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 79px;
}

.timer__circle svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: rotateY(-180deg) rotateZ(-90deg);
  -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
          filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  z-index: 2;
}

.timer__circle circle {
  fill: none;
  stroke: #fff;
  stroke-width: 31px;
  stroke-linecap: round;
  box-shadow: 4px 4px 40px 40px #000;
  transition: 1s linear;
}

.timer__count {
  font-family: Mansalva;
  font-size: 96px;
  color: #fff;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.timer__song {
  font-size: 48px;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  color: #fff;
  font-weight: bold;
}

.timer__btn {
  padding: 24px 89px;
  margin-right: 21px;
}

.timer__btn::after {
  display: none;
}

.timer__btn--output {
  background-color: transparent;
  color: #11999e;
  border: 3px solid #11999e;
  margin: 0;
  transition: .3s;
}

.timer__btn--output:hover {
  background-color: #11999e;
  color: #fff;
}
/*# sourceMappingURL=timer.css.map */
